@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500&family=Poppins:wght@300;400;500&family=Roboto:wght@100;300;400;500&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style-type: none;
  outline: none;
}

html {
  font-size: 62.5%;
}

body {
  background: url('./assets/background.jpg');
  background-repeat: repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  overflow-x: scroll;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  font-size: 4rem;
  letter-spacing: 0.1rem;
  font-weight: 600;
  color: #08292a;
  text-shadow: 0 0.2rem 5rem rgb(119, 115, 115);
  text-transform: uppercase;
  text-align: center;
  margin: 2rem 0;
}

.search-form {
  width: 40rem;
  height: 7.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #d1e8e2;
  border-radius: 0.6rem;
  margin-bottom: 5rem;
  box-shadow: 1rem 2rem 5rem rgb(65, 60, 60);
  position: relative;
}

.alert {
  position: absolute;
  top: -3rem;
  left: 0;
  background-color: rgb(235, 111, 111);
  width: calc(100% - 0.4rem);
  border-radius: 0.5rem;
  padding: 0.2rem;
}

.alert h3 {
  font-size: 1.8rem;
  color: #fff;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
}

.search-form input {
  font-size: 1.6rem;
  border: none;
  border-bottom: 0.1rem solid #ccc;
  margin: 0 0.5rem;
  border-radius: 0.5rem;
  height: 3rem;
}

.search-form input[type='text'] {
  padding: 0 0.5rem;
  width: 60%;
  color: rgb(219, 205, 205);
}

.search-form input[type='submit'] {
  width: 25%;
  text-transform: uppercase;
  background-color: #116466;
  color: #fff;
}

.recipes {
  margin-top: 0.2rem;
  width: 90%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
}

.recipe {
  width: 25rem;
  margin: 1rem;
  display: flex;
  flex-direction: column;
  background-color: #d3d3d3;
  padding: 2rem;
  position: relative;
  border-radius: 0.5rem;
  box-shadow: 2rem 3rem 5rem rgb(98, 98, 98);
}

.recipe h2 {
  background-color: rgba(161, 161, 161, 0.6);
  border-radius: 0.5rem;
  padding: 0.3rem 0.5rem;
  position: absolute;
  top: 2.5rem;
  left: 2.5rem;
  right: 2.5rem;
  font-size: 2rem;
  font-weight: 400;
  text-shadow: 0 0.5rem 0.5rem #555;
  color: #fff;
}

.recipe img {
  border-radius: 0.6rem;
  object-fit: cover;
}

.recipe a {
  font-size: 1.8rem;
  color: #40b48e;
}

.recipe button {
  font-size: 2rem;
  text-transform: uppercase;
  color: #fff;
  background-color: #188d8f;
  border: none;
  border-radius: 0.5rem;
  height: 3rem;
}

.ingredient-list {
  margin-top: 1rem;
  background-color: #c6ab92;
  border-radius: 0.5rem;
  padding: 0.5rem;
}

.ingredient-list li {
  margin: 0.5rem 0;
}

.ingredient-text {
  font-size: 1.5rem;
  color: rgb(40, 38, 38);
}

.ingredient-weight {
  color: #973ec0;
  font-size: 1.3rem;
}
